<template>
  <div class="row">
    <div class="col-xs-offset-2 col-xs-8">
      <div class="page-header"><h2>Vue Router Demo</h2></div>
      <button @click="pre">上一步</button>
      <button @click="next">下一步</button>
      <button @click="go">go</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Banner",
  methods: {
    pre() {
      this.$router.back()
    },
    next() {
      this.$router.forward()
    },
    go() {
      this.$router.go(2)
    },

  }
};
</script>

